import {mapState} from 'vuex';
import list from '@/components/contacts/list/index.vue'
import _ from "underscore";
import preview from '@/components/contacts/preview/index.vue'
import contact from '@/components/contact/index.vue'

import f from '@/application/functions.js'

export default {
  name: 'contacts',
  props: {
    source: String,
    mode: {
      type: String,
      default: function () {
        return ''
      }
    },
    title: String,
    chatRoomId: String
  },

  components: {
    list,
    contact
  },

  data: function () {

    return {
      aboutShow: false,
      loading: false,
      fromSearch: [],
      myContacts: [],
      inputText: '',
      groupContacts: [],
      newContactGroup: [],
      groupName: '',
      contactsPage: 'Contacts',
      showGroupsActions: false,
      contact: {}
    }

  },

  created: () => {

  },
  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    ...mapState([
      'contactsMap',
      'pocketnet',
      'minimized',
      'active'
    ]),

    u: function () {
      console.log(this.$route.query.u, "UUUUUUUUU ITS U")
      return this.$route.query.u
    },

    contactsList: function () {
      let client = this.core.mtrx.client

      _.mapObject(this.contactsMap, function (key, value) {
        key.selected = false
        if (client.getUser(`@${value}:matrix.pocketnet.app`) !== null) {
          key.isUser = true
        } else {
          key.isUser = false
        }
      })

      return this.contactsMap

    },
    direct: function () {
      if (this.groupContacts.length === 1) {
        return true
      }
    },
    tetatetid: function () {
      return this.core.mtrx.kit.tetatetid(this.contact, this.core.user.userinfo)
    },
    gName: function () {
      let name = this.groupName
      return name.replace(/ /g, '_')
    },
  }),
  methods: {
    chat: function () {

      this.core.mtrx.kit.tetatetid(this.contact, this.core.user.userinfo)

    },
    removeEl(contact) {
      let self = this
      this.groupContacts.splice(self.groupContacts.indexOf(contact), 1)
      this.fromSearch.forEach(function (user) {
        if (user.id === contact.id) {
          contact.selected = !user.selected;
          self.fromSearch.splice(self.fromSearch.indexOf(contact), 1, contact)
        }
        console.log(self.groupContacts, "after remove grup user")
        console.log(self.fromSearch, "after remove")
      })
    },
    addContact(contact) {
      let self = this
      // _.mapObject(self.contactsList, function (contactUser){
      //   contactUser.selected = true
      // })
      console.log(contact)
      this.fromSearch.forEach(function (user) {

        if (user.id === contact.id) {
          contact.selected = !user.selected;
          self.fromSearch.splice(self.fromSearch.indexOf(contact), 1, contact)
          console.log(self.groupContacts.indexOf(contact) !== -1, "user Selected")
          if(!(self.groupContacts.indexOf(contact) !== -1)){
            self.groupContacts.push(contact)
            console.log("PUSHHHH////////")
          }else {
            self.groupContacts.splice(self.groupContacts.indexOf(contact), 1)
          }
        }

        console.log(self.groupContacts)

      })
      console.log(this.fromSearch, "from SERRRRRch")
    },

    search(text) {
      console.log(this.mode)
      let contacts = this.contactsMap
      console.log(typeof(contacts), "which type")
      let self = this
      this.inputText = text
      this.core.user.searchContacts(text).then(users => {
        users.forEach(function (user){
         _.mapObject(contacts,function (id, contact){
           if(id.id === user.id){
             users.splice(users.indexOf(user), 1)
           }
         })
        })
        this.fromSearch = users
        console.log(this.fromSearch, "from Search is ready")
      })
    },

    createGroup(users) {
      const data = this.core.mtrx.kit.groupId(users)
      if (this.groupName.length !== 0) {
        console.log('create room 1')
        return this.core.mtrx.client.createRoom({
          room_alias_name: '#' + data.hash,
          visibility: 'private',
          invite: data.idForInviting,
          name: '#' + this.groupName,
          initial_state: [
            {
              "type": "m.room.guest_access",
              "state_key": "",
              "content": {
                "guest_access": "can_join"
              }
            },
            {
              "type": "m.room.encryption",
              "state_key": "",
              "content": {
                "algorithm": "m.megolm.v1.aes-sha2"
              }
            }
          ]
        }).then(chat => {

          this.$router.push({
            path: 'chat',
            query: {'id': chat.room_id}
          })
        })

      } else {
        console.log('create room 2')
        return this.core.mtrx.client.createRoom({
          room_alias_name: '#' + data.hash,
          visibility: 'private',
          invite: data.idForInviting,
          name: '#' + "New Room",
          initial_state: [
            {
              "type": "m.room.guest_access",
              "state_key": "",
              "content": {
                "guest_access": "can_join"
              }
            },
            {
              "type": "m.room.encryption",
              "state_key": "",
              "content": {
                "algorithm": "m.megolm.v1.aes-sha2"
              }
            }
          ]
        }).then(chat => {

          this.$router.push({
            path: 'chat',
            query: {'id': chat.room_id}
          })
          // http://localhost:8080/chat?id=%21ehBaimKfwFIonXIoJT%3Amatrix.pocketnet.app
        })
      }
    },
    inviteUserAction(users) {
      let client = this.core.mtrx.client
      let roomID = this.chatRoomId
      users.forEach(function (user) {
        let matrixID = '@' + `${user.id}` + ':matrix.pocketnet.app'
        client.invite(roomID, matrixID).then(r => {
        })
      })
      this.$emit('closeModal', false)
    }
  },
}